"use client";

import { MonitorCog, Save, X } from "lucide-react";
import { motion, useDragControls } from "framer-motion";
import Ripple from "material-ripple-effects";
import { useEffect, useRef, useState } from "react";
import { Input } from "@heroui/input";
import axiosApi from "@/config/axios-api";
import { addToast } from "@heroui/toast";

export default function ModalSettings({ title = "ตั้งค่า", onClose }) {
    const ripple = new Ripple();
    const boundsRef = useRef(null);
    const dragControls = useDragControls();
    const [ inputValue, setInputValue ] = useState({
        hospitalName: "",
        hospitalCode: ""
    });
    const [ isLoading, setIsLoading ] = useState(false);
    const [isHover, setIsHover] = useState(false);

    const modalRef = useRef(null);

    let token = localStorage.getItem("token");
    
    useEffect(() => {
        if (!token) return
        fetchData();
    }, [])

    const fetchData = async () => {
        try {
            const res = await axiosApi.get("/setting/hcode");

            setInputValue({
                hospitalName: res.data.data[0].hcode_name || "",
                hospitalCode: res.data.data[0].hcode || ""
            });

            console.log(res.data.data[0].hcode_name);
        } catch (err) {
            console.log(err)
        }
        // ดึงข้อมูลมาแสดงในฟอร์ม
    }

    const isMobile = typeof window !== "undefined" && window.innerWidth < 768; 
    // หรือจะใช้ useMediaQuery/react-responsive ก็ได้

    const hdlSubmit = async (e) => {
        setIsLoading(true);
        e.preventDefault();
        try {
            const res = await axiosApi.post("/setting/hcode", {
                hcode_name: inputValue.hospitalName,
                hcode: parseInt(inputValue.hospitalCode)
            }, {
                headers: { Authorization: `Bearer ${token}` }
            })

            if(res.status === 200){
                addToast({
                    title: "บันทึกข้อมูลสำเร็จ",
                    description: "ตั้งค่าข้อมูลโรงพยาบาลเรียบร้อยแล้ว",
                    color: "success",
                    duration: 5000,
                    shouldShowTimeoutProgress: true,
                });
                window.location.replace(window.location.pathname); // รีเฟรชหน้า
            }
        } catch (err) {
            console.log(err)
            addToast({
                title: "เกิดข้อผิดพลาด",
                description: err?.response?.data?.message || err?.message || "ไม่สามารถบันทึกข้อมูลได้ กรุณาลองใหม่อีกครั้ง",
                color: "danger",
                duration: 5000,
                shouldShowTimeoutProgress: true,
            });
        } finally {
            // อะไรสักอย่าง
            setIsLoading(false);
        }
    };

    return (
        <motion.div
            ref={boundsRef}
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className={`fixed inset-0 flex items-center justify-center z-50 backdrop-blur-xs bg-black/10 dark:bg-black/20" px-4`}
        >
            <motion.div
                drag={!isMobile}                   // ถ้าเป็นมือถือปิดการลาก
                dragControls={dragControls}
                dragConstraints={boundsRef}
                dragListener={false}
                dragElastic={0}
                dragMomentum={false}
                dragTransition={{ bounceStiffness: 600, bounceDamping: 20 }}
                initial={{ opacity: 0, scale: 0.5 }}
                animate={{ opacity: 1, scale: 1 }}
                className="max-w-md rounded-2xl w-full shadow-lg overflow-hidden will-change-transform"
                style={{ touchAction: "none" }}
                // ref={modalRef}
                // onMouseEnter={() => setIsHover(true)}
                // onMouseLeave={() => setIsHover(false)}
            >
                <div 
                    className="bg-white dark:bg-[#1f1f1f] rounded-2xl border border-black/10 dark:border-[#2C2C2C] px-2 pb-2">
                    <div
                        className="flex items-center justify-between py-2 select-none cursor-grab active:cursor-grabbing"
                        onPointerDown={(e) => {
                        if (!isMobile) dragControls.start(e); // กดลากได้เฉพาะ desktop
                        }}
                    >
                        <p className="font-semibold text-sm rounded-full px-4 py-1.5 flex gap-1 items-center line-clamp-1 bg-zinc-200 dark:bg-zinc-700">
                            <MonitorCog size={16} />{title}
                        </p>

                        <motion.button
                        whileTap={{ scale: 0.98 }}
                        onMouseUp={(e) => ripple.create(e)}
                        onClick={onClose}
                        onPointerDownCapture={(e) => e.stopPropagation()}
                        className="ml-2 text-zinc-400 rounded-full p-1.5 hover:bg-zinc-100 dark:hover:bg-zinc-800 transition cursor-pointer"
                        aria-label="ปิด"
                        >
                            <X size={16} />
                        </motion.button>
                    </div>

                    <motion.form
                        onSubmit={hdlSubmit}
                        className="flex flex-col gap-1 px-2 pt-1 max-h-[calc(100vh-40vh)] overflow-y-auto select-none">
                        <Input label="ชื่อโรงพยาบาล" labelPlacement="outside-top" variant="flat" radius="lg" value={inputValue.hospitalName} onValueChange={(e) => setInputValue({ ...inputValue, hospitalName: e })} isRequired />
                        <Input label="รหัสโรงพยาบาล" labelPlacement="outside-top" variant="flat" radius="lg" value={inputValue.hospitalCode} onValueChange={(e) => setInputValue({ ...inputValue, hospitalCode: e })} isRequired />
                        <div className="flex justify-end gap-2 mt-2">
                            <motion.button
                                whileTap={{ scale: 0.98 }}
                                onMouseDown={(e) => ripple.create(e)}
                                type="button"
                                onClick={onClose}
                                className="px-6 py-1.5 bg-zinc-200 dark:bg-zinc-700 text-zinc-600 dark:text-zinc-200 
                                rounded-2xl hover:bg-zinc-300 dark:hover:bg-zinc-600 transition cursor-pointer
                                
                                "
                            >
                                ยกเลิก
                            </motion.button>
                            <motion.button
                                whileTap={{ scale: 0.98 }}
                                onMouseDown={(e) => ripple.create(e)}
                                type="submit"
                                disabled={isLoading}
                                className="px-6 py-1.5 bg-emerald-500 dark:bg-emerald-700
                                text-white rounded-2xl dark:hover:bg-emerald-800 hover:bg-emerald-700
                                transition cursor-pointer
                                flex items-center gap-1
                                disabled:opacity-50 disabled:cursor-not-allowed
                                "
                            >
                                <Save size={16} strokeWidth={1.5} /> บันทึก
                            </motion.button>
                        </div>
                    </motion.form>
                </div>
            </motion.div>
        </motion.div>
    );
}
